html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8; }

p {
  color: #000000;
  font-size: 2rem; }

a {
  text-decoration: none; }

img,
picture {
  max-width: 100%;
  height: auto;
  display: block; }

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center; }

h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.4rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.6rem; }

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem; }

.content-centric {
  max-width: 800px; }

.align-right {
  display: flex;
  justify-content: flex-end; }

.yellow-button {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 1.66667rem;
  display: inline-block; }
  .yellow-button:hover {
    background-color: #c77808; }

.yellow-button-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 1.66667rem;
  display: block; }
  .yellow-button-block:hover {
    background-color: #c77808; }

.green-button {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 1.66667rem;
  display: inline-block; }
  .green-button:hover {
    background-color: #619800; }

/* UI */
.no-webp .inicio {
  background-image: url("../img/header.jpg"); }

.webp .inicio {
  background-image: url("../img/header.webp"); }

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0; }
  .header.inicio {
    background-position: center;
    background-size: cover; }
    @media (min-width: 768px) {
      .header.inicio {
        min-height: 70rem; } }
    @media (min-width: 768px) {
      .header.inicio .content-header {
        min-height: 70rem; } }

.content-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  .content-header h1 {
    text-align: left;
    color: #FFFFFF;
    font-weight: 700;
    line-height: 2;
    max-width: 60rem; }

.bar {
  display: flex;
  flex-direction: column;
  padding-top: 3rem; }
  @media (min-width: 768px) {
    .bar {
      flex-direction: row;
      justify-content: space-between; } }

@media (min-width: 768px) {
  .navegation {
    align-items: center;
    display: flex; } }

.navegation a {
  color: #FFFFFF;
  display: block;
  font-size: 2.2rem;
  text-align: center;
  text-decoration: none; }
  .navegation a:hover {
    color: #71B100; }
  @media (min-width: 768px) {
    .navegation a {
      font-size: 1.8rem;
      margin-right: 2rem; }
      .navegation a:last-of-type {
        margin: 0; } }

.footer {
  background-color: #333333;
  padding: 2.5rem 0; }
  .footer .container-footer {
    padding: 2.5rem; }
  .footer .copyright {
    color: #FFFFFF;
    text-align: center; }

@media (min-width: 768px) {
  .us-icons {
    display: grid;
    column-gap: 2rem;
    grid-template-columns: repeat(3, 1fr); } }

.icon {
  text-align: center; }
  .icon img {
    height: 10rem;
    margin: 0 auto; }
  .icon h3 {
    text-transform: uppercase; }

@media (min-width: 768px) {
  .container-ads {
    display: grid;
    column-gap: 2rem;
    grid-template-columns: repeat(3, 1fr); } }

.ad {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8; }
  .ad .container-ad {
    padding: 2rem; }
  .ad h3, .ad p {
    margin: 0; }
  .ad .price {
    color: #71B100;
    font-weight: 700;
    font-size: 2.4rem; }

.characteristic-icons {
  display: flex;
  list-style: none;
  padding: 0;
  justify-content: space-evenly; }
  .characteristic-icons li {
    flex: 1;
    display: flex;
    justify-content: center; }
    .characteristic-icons li img {
      margin-right: 1rem; }
    .characteristic-icons li p {
      font-weight: 900; }

.no-web .img-contact {
  background-image: url("../img/encuentra.jpg"); }

.webp .img-contact {
  background-image: url("../img/encuentra.webp"); }

.img-contact {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .img-contact h2 {
    font-size: 4rem;
    font-weight: bolder;
    color: #FFFFFF; }
  .img-contact p {
    font-size: 1.8rem;
    color: #FFFFFF; }
