html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $main_font;
    font-size: 1.6rem;
    line-height: 1.8;
}
p {
    color: $black;
    font-size: 2rem;
}


a {
    text-decoration: none;
}

img,
picture {
    max-width: 100%;
    // width: 35rem;
    height: auto;
    display: block;
}

h1, h2, h3 {
    font-family: $main-font;
    margin: $separation / 2 0;
    font-weight: 300;
    text-align: center;
    
}

h1 {
    font-size: 3.8rem;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 3rem;
}
h4 {
    font-size: 2.6rem;
}