.no-web .img-contact {
  background-image: url('../img/encuentra.jpg');
}

.webp .img-contact {
  background-image: url('../img/encuentra.webp');

}
.img-contact {
  background-position: center center;
  background-size: cover;
  padding: $separation 0;
  display: flex;
  flex-direction: column;
  align-items: center;

  h2 {
    font-size: 4rem;
    font-weight: bolder;
    color: $white;
  }

  p {
    font-size: 1.8rem;
    color: $white;
  }
}