/** Media Queries **/
@mixin phone {
    @media (min-width: #{$phone}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}
@mixin grid($columns, $spacing ) {
    display: grid;
    column-gap: $spacing;
    grid-template-columns: repeat($columns, 1fr);
}

@mixin button($bgColor, $display) {
    background-color: $bgColor;
    color: $white;
    font-weight: $bold;
    text-decoration: none;
    padding: 1rem 3rem;
    text-align: center;
    margin-top: $separation / 3;
    display: $display;

    &:hover {
        background-color: darken($bgColor, 5);
    }
}
